<template>
  <v-container fluid fill-height>
    <v-layout align-center justify-center>
      <v-flex xs12 sm8 md4 lg3>
        <v-card class="elevation-12">
          <v-toolbar dense flat dark color="primary">
            <v-layout>
              <v-toolbar-title>
                <h6>
                  {{loginTitle}}
                </h6>
              </v-toolbar-title>
            </v-layout>
          </v-toolbar>
          <v-card-text>
            <v-layout row wrap mt-3 mb-2 justify-center>
              <v-flex xs8>
                <img class="logo_img" :src="require('@/assets/logo.png')"/>
              </v-flex>
              <v-flex xs10>
                <v-form>
                  <v-text-field
                    v-model="userId" 
                    prepend-icon="person"
                    label="ID" 
                    type="text"
                    data-vv-name="Id"
                    :error-messages="errors.collect('Id')"
                    v-validate="'required|min:2|max:20'"
                  ></v-text-field>
                  <v-text-field
                    v-model="password" 
                    prepend-icon="lock"
                    label="Password"
                    type="password"
                    data-vv-name="Password"
                    :error-messages="errors.collect('Password')"
                    v-validate="'required|min:6'"
                    v-on:keyup.enter="login()"
                  ></v-text-field>
                  <v-select
                    v-model="selectLanguage"
                    prepend-icon="language"
                    :items="language"
                    label="Standard"
                  ></v-select>
                </v-form>
              </v-flex>
              <v-flex xs12>
                <v-layout align-center justify-center>
                  <v-flex xs12>
                    <v-btn class="login_sub_btn" flat @click.stop="idFindDialog = true">ID찾기</v-btn>
                    <v-btn class="login_sub_btn" flat @click.stop="passwordFindDialog = true">비밀번호 찾기</v-btn>
                    <v-btn class="login_sub_btn" flat @click.stop="userAddFindDialog = true">사용자 등록</v-btn>
                  </v-flex>
                  <v-flex>
                    <v-dialog
                      v-model="idFindDialog"
                      max-width="400"
                    >
                      <v-card>
                        <v-layout fluid column fill-height pa-4 pl-2 pr-2>
                          <v-card-title class="headline">
                            ID 찾기
                          </v-card-title>
                          <v-card-text>
                            등록하신 이름과 Email을 입력하세요.
                          </v-card-text>
                          <v-flex ml-3 mr-3>
                            <v-text-field
                              v-model="findUserName" 
                              prepend-icon="person"
                              label="Name" 
                              type="text"
                              data-vv-name="Name"
                              :error-messages="errors.collect('Name')"
                              v-validate="'required|min:2|max:20'"
                            ></v-text-field>
                          </v-flex>
                          <v-flex ml-3 mr-3 mb-4>
                            <v-text-field
                              v-model="findUserEmail" 
                              prepend-icon="email"
                              label="E-mail" 
                              type="text"
                              data-vv-name="Email"
                              :error-messages="errors.collect('Email')"
                              v-validate="'required|email'"
                            ></v-text-field>
                          </v-flex>

                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="primary" outline @click="idFindDialog = false">
                              취소
                            </v-btn>

                            <v-btn color="primary">
                              확인
                            </v-btn>
                          </v-card-actions>
                        </v-layout>
                      </v-card>
                    </v-dialog>
                  </v-flex>
                  <v-flex>
                    <v-dialog
                      v-model="passwordFindDialog"
                      max-width="400"
                    >
                      <v-card>
                        <v-layout fluid column fill-height pa-4 pl-2 pr-2>
                          <v-card-title class="headline">
                            비밀번호 찾기
                          </v-card-title>
                          <v-card-text>
                            등록하신 ID와 Email을 입력하세요.
                          </v-card-text>
                          <v-flex ml-3 mr-3>
                            <v-text-field
                              v-model="findUserId" 
                              prepend-icon="person"
                              label="ID" 
                              type="text"
                              data-vv-name="Id"
                              :error-messages="errors.collect('Id')"
                              v-validate="'required|min:2|max:20'"
                            ></v-text-field>
                          </v-flex>
                          <v-flex ml-3 mr-3 mb-4>
                            <v-text-field
                              v-model="findUserEmail" 
                              prepend-icon="email"
                              label="E-mail" 
                              type="text"
                              data-vv-name="Email"
                              :error-messages="errors.collect('Email')"
                              v-validate="'required|email'"
                            ></v-text-field>
                          </v-flex>

                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="primary" outline @click="passwordFindDialog = false">
                              취소
                            </v-btn>

                            <v-btn color="primary">
                              확인
                            </v-btn>
                          </v-card-actions>
                        </v-layout>
                      </v-card>
                    </v-dialog>
                  </v-flex>
                  <v-flex>
                    <v-dialog
                      v-model="userAddFindDialog"
                      max-width="550"
                    >
                      <v-card>
                        <v-layout fluid column fill-height pa-4 pl-2 pr-2>
                          <v-card-title class="headline">
                            사용자 등록
                          </v-card-title>
                          <v-flex ml-3 mr-3>
                            <v-layout row>
                              <v-text-field
                                v-model="addUserId" 
                                prepend-icon="person"
                                label="ID" 
                                type="text"
                                data-vv-name="Id"
                                :error-messages="errors.collect('Id')"
                                v-validate="'required|min:2|max:20'"
                              ></v-text-field>
                              <v-btn color="secondary">
                                ID 중복체크
                              </v-btn>
                            </v-layout>
                          </v-flex>
                          <v-flex ml-3 mr-3>
                            <v-text-field
                              v-model="addUserPassword" 
                              prepend-icon="lock"
                              label="비밀번호" 
                              type="password"
                              data-vv-name="Password"
                              :error-messages="errors.collect('Password')"
                              v-validate="'required|min:6'"                              
                              ref="password"
                            ></v-text-field>
                          </v-flex>
                          <v-flex ml-3 mr-3>
                            <v-text-field
                              v-model="addUserPasswordConfirm" 
                              prepend-icon="done"
                              label="비밀번호확인" 
                              type="password"
                              data-vv-name="Password_confirmation"
                              :error-messages="errors.collect('Password_confirmation')"
                              v-validate="'required|confirmed:password'"
                            ></v-text-field>
                          </v-flex>
                          <v-flex ml-3 mr-3>
                            <v-layout row>
                              <v-flex>
                                <v-text-field
                                  v-model="addUserName" 
                                  prepend-icon="account_circle"
                                  label="이름" 
                                  type="text"
                                  data-vv-name="Name"
                                  :error-messages="errors.collect('Name')"
                                  v-validate="'min:2|max:20'"
                                ></v-text-field>
                              </v-flex>
                              <v-flex ml-2>
                                <v-text-field
                                  v-model="addUserMiddleName"
                                  label="중간이름" 
                                  type="text"
                                  data-vv-name="MiddleName"
                                  :error-messages="errors.collect('MiddleName')"
                                  v-validate="'min:2|max:20'"
                                ></v-text-field>
                              </v-flex>
                              <v-flex ml-2>
                                <v-text-field
                                  v-model="addUserFamilyName"
                                  label="성" 
                                  type="text"
                                  data-vv-name="FamilyName"
                                  :error-messages="errors.collect('FamilyName')"
                                  v-validate="'required|min:2|max:20'"
                                ></v-text-field>
                              </v-flex>
                            </v-layout>
                          </v-flex>
                          <v-flex ml-3 mr-3>
                            <v-text-field
                              v-model="addUserEmail" 
                              prepend-icon="email"
                              label="E-mail" 
                              type="text"
                              data-vv-name="Email"
                              :error-messages="errors.collect('Email')"
                              v-validate="'required|email'"
                            ></v-text-field>
                          </v-flex>
                          <v-flex ml-3 mr-3>
                            <v-text-field
                              v-model="addUserPhone" 
                              prepend-icon="phone"
                              label="전화번호" 
                              type="text"
                              data-vv-name="Phone"
                              :error-messages="errors.collect('Phone')"
                              v-validate="'min:2|max:20'"
                            ></v-text-field>
                          </v-flex>
                          <v-flex ml-3 mr-3>
                            <v-text-field
                              v-model="addUserOfficeNumber" 
                              prepend-icon="business"
                              label="사무실 번호" 
                              type="text"
                              data-vv-name="OfficNumber"
                              :error-messages="errors.collect('OfficNumber')"
                              v-validate="'min:2|max:20'"
                            ></v-text-field>
                          </v-flex>
                          <v-flex ml-3 mr-3>
                            <v-layout row wrap>
                              <v-flex xs4>
                                <v-text-field
                                  v-model="addUserPostalNumber" 
                                  prepend-icon="home"
                                  label="우편번호" 
                                  type="text"
                                  data-vv-name="PostalNumber"
                                  :error-messages="errors.collect('PostalNumber')"
                                  v-validate="'min:2|max:20'"
                                ></v-text-field>
                              </v-flex>
                              <v-flex ml-2>
                                <v-text-field
                                  v-model="addUserAddress"
                                  label="주소" 
                                  type="text"
                                  data-vv-name="Address"
                                  :error-messages="errors.collect('Address')"
                                  v-validate="'required|min:2|max:20'"
                                ></v-text-field>
                              </v-flex>
                              <v-flex xs12>
                                <v-text-field
                                  prepend-icon="home"
                                  v-model="addUserAddressDetail"
                                  label="상세주소" 
                                  type="text"
                                  data-vv-name="AddressDetail"
                                  :error-messages="errors.collect('AddressDetail')"
                                  v-validate="'min:2|max:20'"
                                ></v-text-field>
                              </v-flex>
                            </v-layout>
                          </v-flex>
                          <v-flex ml-3 mr-3>
                            <v-select
                              v-model="addUserLanguage"
                              prepend-icon="language"
                              :items="language"
                              label="주사용 언어"
                            ></v-select>
                          </v-flex>
                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="primary" outline @click="userAddFindDialog = false">
                              메인페이지로 이동
                            </v-btn>

                            <v-btn color="primary">
                              등록
                            </v-btn>
                          </v-card-actions>
                        </v-layout>
                      </v-card>
                    </v-dialog>
                  </v-flex>
                </v-layout>
              </v-flex>
            </v-layout>
          </v-card-text>
          <v-card-actions>
            <v-layout mb-5 mt-2 justify-center>
              <v-btn color="primary" class="login_btn" large v-on:click="login()">Login</v-btn>
            </v-layout>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data: () => ({
      loginTitle: 'sccho Test',
      idFindDialog: false,
      passwordFindDialog: false,
      userAddFindDialog: false,
      findUserName: null,
      findUserId: null,
      findUserEmail: null,
      addUserId: null,
      addUserPassword: null,
      addUserPasswordConfirm: null,
      addUserEmail: null,
      addUserName: null,
      addUserMiddleName: null,
      addUserFamilyName: null,
      addUserPhone: null,
      addUserOfficeNumber: null,
      addUserPostalNumber: null,
      addUserAddress: null,
      addUserAddressDetail: null,
      addUserLanguage: null,
      language:['English','한국어'],
      selectLanguage:'한국어',
      userId:'sccho',
      password: '12345'
    }),
    mounted(){
    },
    methods:{
      login(){
        if(this.userId === this.$store.state.userId && this.password === this.$store.state.password){
          this.$store.dispatch('login');
          // this.$isAuth = true;
          this.$router.push('/');
        }if(this.userId !== this.$store.state.userId){
          this.$toastr.error( '사용자 ID가 존재하지 않습니다.', '사용자 ID 오류',{timeOut: 1500});
        }if(this.password !== this.$store.state.password){
          this.$toastr.error( '비밀번호를 확인 후 입력해주세요.', '비밀번호 오류',{timeOut: 1500});
        }
      }
    }
  }
</script>

<style>
.login_btn{
  padding: 0 6rem;
}
.login_sub_btn{
  font-weight: 600;
  color: #1e3765 !important;
}
</style>
